<template>
  <div v-if="visible" class="upload-dialog-overlay" @click="closeDialog">
    <div class="upload-dialog" @click.stop>
      <div class="upload-header">
        <h3>上传文件到：{{ currentPath || '根目录' }}</h3>
        <button class="close-btn" @click="closeDialog">&times;</button>
      </div>
      
      <!-- 上传限制信息 -->
      <div class="upload-limits" v-if="uploadLimits">
        <div class="limits-info">
          <span>单文件最大：{{ uploadLimits.max_file_size_formatted }}</span>
          <span v-if="!uploadLimits.is_unlimited_user">
            可用空间：{{ uploadLimits.available_space_formatted }}
          </span>
          <span v-else class="unlimited-badge">
            存储空间：无限制
          </span>
          <span>最多同时上传：{{ uploadLimits.max_files_per_upload }} 个文件</span>
        </div>
        
        <!-- 允许的文件扩展名 -->
        <div class="allowed-extensions">
          <button class="extensions-toggle" @click="showExtensions = !showExtensions">
            <i class="fas fa-file-alt"></i>
            支持的文件类型 
            <i class="fas" :class="showExtensions ? 'fa-chevron-up' : 'fa-chevron-down'"></i>
          </button>
          <div v-if="showExtensions" class="extensions-list">
            <span v-for="ext in uploadLimits.allowed_extensions" :key="ext" class="extension-tag">
              .{{ ext }}
            </span>
          </div>
        </div>
        
        <div v-if="!uploadLimits.is_unlimited_user && uploadLimits.storage_usage_percentage > 0" class="storage-usage">
          <div class="usage-bar">
            <div 
              class="usage-fill" 
              :style="{ width: uploadLimits.storage_usage_percentage + '%' }"
              :class="getUsageBarClass(uploadLimits.storage_usage_percentage)"
            ></div>
          </div>
          <span class="usage-text">
            存储使用率：{{ uploadLimits.storage_usage_percentage }}% 
            ({{ uploadLimits.used_space_formatted }} / {{ uploadLimits.user_storage_limit_formatted }})
          </span>
        </div>
      </div>
      
      <!-- 文件选择区域 -->
      <div 
        class="file-drop-zone" 
        :class="{ 'drag-over': isDragOver }"
        @drop="handleDrop"
        @dragover.prevent="handleDragOver"
        @dragleave="handleDragLeave"
        @click="triggerFileSelect"
      >
        <div class="drop-zone-content">
          <div class="upload-icon">📁</div>
          <p class="drop-text">拖拽文件到此处或点击选择文件</p>
          <p class="drop-hint">支持多文件同时上传</p>
        </div>
        <input 
          ref="fileInput"
          type="file" 
          multiple 
          style="display: none"
          @change="handleFileSelect"
          :accept="allowedExtensions"
        />
      </div>
      
      <!-- 文件列表 -->
      <div class="file-list" v-if="selectedFiles.length > 0">
        <h4>待上传文件 ({{ selectedFiles.length }})</h4>
        <div class="file-items">
          <div 
            v-for="(file, index) in selectedFiles" 
            :key="index"
            class="file-item"
            :class="{ 
              'uploading': file.uploading, 
              'success': file.success, 
              'error': file.error,
              'waiting': !file.uploading && !file.success && !file.error && uploading
            }"
          >
            <div class="file-info">
              <span class="file-name">{{ file.name }}</span>
              <span class="file-size">{{ formatFileSize(file.size) }}</span>
              <span v-if="file.uploading" class="upload-status">正在上传...</span>
              <span v-else-if="!file.uploading && !file.success && !file.error && uploading" class="waiting-status">等待中</span>
              <span v-else-if="file.error" class="error-message">{{ file.errorMessage }}</span>
            </div>
            <div class="file-status">
              <div v-if="file.uploading" class="progress-container">
                <div class="progress-bar">
                  <div class="progress-fill" :style="{ width: file.progress + '%' }"></div>
                </div>
                <span class="progress-text">{{ file.progress }}%</span>
                <div class="uploading-spinner"></div>
              </div>
              <span v-else-if="file.success" class="status-success">✓</span>
              <span v-else-if="file.error" class="status-error" :title="file.errorMessage">✗</span>
              <button v-else class="remove-btn" @click="removeFile(index)">×</button>
            </div>
          </div>
        </div>
      </div>
      
      <!-- 操作按钮 -->
      <div class="upload-actions">
        <button 
          class="btn-secondary" 
          @click="clearFiles"
          :disabled="uploading"
        >
          清空列表
        </button>
        <button 
          class="btn-primary" 
          @click="startUpload"
          :disabled="selectedFiles.length === 0 || uploading"
        >
          {{ uploading ? '上传中...' : `开始上传 (${selectedFiles.length})` }}
        </button>
      </div>
      
      <!-- 上传进度总览 -->
      <div class="upload-summary" v-if="uploadSummary.total > 0">
        <div class="summary-stats">
          <span>总计：{{ uploadSummary.total }}</span>
          <span>成功：{{ uploadSummary.success }}</span>
          <span>失败：{{ uploadSummary.error }}</span>
          <span>进行中：{{ uploadSummary.uploading }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
/* eslint-disable no-undef */
import { ref, computed, watch, inject } from 'vue';
import { apiCall } from '@/utils/auth.js';

// 注入全局组件
const notification = inject('notification');

// Props
// eslint-disable-next-line no-undef
const props = defineProps({
  visible: Boolean,
  currentPath: String
});

// Emits
// eslint-disable-next-line no-undef
const emit = defineEmits(['close', 'uploaded']);

// Refs
const fileInput = ref(null);
const isDragOver = ref(false);
const uploading = ref(false);
const selectedFiles = ref([]);
const uploadLimits = ref(null);
const showExtensions = ref(false);

// 允许的文件扩展名
const allowedExtensions = computed(() => {
  if (!uploadLimits.value?.allowed_extensions) return '';
  return uploadLimits.value.allowed_extensions.map(ext => `.${ext}`).join(',');
});

// 存储使用率样式类
const getUsageBarClass = (percentage) => {
  if (percentage >= 90) return 'usage-critical';
  if (percentage >= 75) return 'usage-warning';
  return 'usage-normal';
};

// 上传统计
const uploadSummary = computed(() => {
  const total = selectedFiles.value.length;
  const success = selectedFiles.value.filter(f => f.success).length;
  const error = selectedFiles.value.filter(f => f.error).length;
  const uploading = selectedFiles.value.filter(f => f.uploading).length;
  
  return { total, success, error, uploading };
});

// 监听弹窗显示状态
watch(() => props.visible, (newVal) => {
  if (newVal) {
    loadUploadLimits();
    // 重置状态
    selectedFiles.value = [];
    uploading.value = false;
    isDragOver.value = false;
    showExtensions.value = false;
  }
});

// 加载上传限制信息
const loadUploadLimits = async () => {
  try {
    const result = await apiCall('upload_limits');
    if (result && result.success) {
      uploadLimits.value = result.data;
    }
  } catch (error) {
    console.error('获取上传限制失败:', error);
  }
};

// 关闭弹窗
const closeDialog = () => {
  if (!uploading.value) {
    emit('close');
  }
};

// 拖拽处理
const handleDragOver = (e) => {
  e.preventDefault();
  isDragOver.value = true;
};

const handleDragLeave = () => {
  isDragOver.value = false;
};

const handleDrop = (e) => {
  e.preventDefault();
  isDragOver.value = false;
  
  const files = Array.from(e.dataTransfer.files);
  addFiles(files);
};

// 触发文件选择
const triggerFileSelect = () => {
  if (!uploading.value) {
    fileInput.value?.click();
  }
};

// 文件选择处理
const handleFileSelect = (e) => {
  const files = Array.from(e.target.files);
  addFiles(files);
  // 清空input，允许重复选择同一文件
  e.target.value = '';
};

// 添加文件到列表
const addFiles = (files) => {
  if (!uploadLimits.value) {
    notification.open({
      type: 'error',
      title: '上传限制信息未加载',
      message: '请稍后重试',
      duration: 3000
    });
    return;
  }
  
  const maxFiles = uploadLimits.value.max_files_per_upload;
  const maxFileSize = uploadLimits.value.max_file_size;
  const allowedExts = uploadLimits.value.allowed_extensions;
  
  // 检查文件数量限制
  if (selectedFiles.value.length + files.length > maxFiles) {
    notification.open({
      type: 'warning',
      title: '文件数量超限',
      message: `最多同时上传 ${maxFiles} 个文件`,
      duration: 3000
    });
    return;
  }
  
  const validFiles = [];
  const errorFiles = [];
  
  files.forEach(file => {
    // 检查文件大小
    if (file.size > maxFileSize) {
      errorFiles.push({
        name: file.name,
        reason: `文件过大，超过 ${uploadLimits.value.max_file_size_formatted} 限制`
      });
      return;
    }
    
    // 检查存储空间（非无限制用户）
    if (!uploadLimits.value.is_unlimited_user) {
      const availableSpace = uploadLimits.value.available_space || 0;
      if (file.size > availableSpace) {
        errorFiles.push({
          name: file.name,
          reason: `存储空间不足，需要 ${formatFileSize(file.size)}，可用 ${uploadLimits.value.available_space_formatted}`
        });
        return;
      }
    }
    
    // 检查文件扩展名
    const ext = file.name.split('.').pop()?.toLowerCase();
    if (ext && !allowedExts.includes(ext)) {
      errorFiles.push({
        name: file.name,
        reason: `不支持的文件类型: .${ext}`
      });
      return;
    }
    
    // 检查是否已存在
    const exists = selectedFiles.value.some(f => f.name === file.name && f.size === file.size);
    if (exists) {
      errorFiles.push({
        name: file.name,
        reason: '文件已在列表中'
      });
      return;
    }
    
    validFiles.push({
      file,
      name: file.name,
      size: file.size,
      uploading: false,
      success: false,
      error: false,
      progress: 0,
      errorMessage: ''
    });
  });
  
  // 添加有效文件
  selectedFiles.value.push(...validFiles);
  
  // 显示错误文件提示
  if (errorFiles.length > 0) {
    const errorMsg = errorFiles.map(f => `${f.name}: ${f.reason}`).join('\n');
    notification.open({
      type: 'warning',
      title: `${errorFiles.length} 个文件无法添加`,
      message: errorMsg,
      duration: 5000
    });
  }
  
  if (validFiles.length > 0) {
    notification.open({
      type: 'success',
      title: '文件添加成功',
      message: `已添加 ${validFiles.length} 个文件到上传列表`,
      duration: 2000
    });
  }
};

// 移除文件
const removeFile = (index) => {
  if (!uploading.value) {
    selectedFiles.value.splice(index, 1);
  }
};

// 清空文件列表
const clearFiles = () => {
  if (!uploading.value) {
    selectedFiles.value = [];
  }
};

// 开始上传
const startUpload = async () => {
  if (selectedFiles.value.length === 0 || uploading.value) return;
  
  uploading.value = true;
  
  const uploadPromises = selectedFiles.value.map(async (fileItem) => {
    if (fileItem.success || fileItem.error) return;
    
    fileItem.uploading = true;
    fileItem.progress = 0;
    
    try {
      const formData = new FormData();
      formData.append('action', 'upload');
      formData.append('file', fileItem.file);
      formData.append('path', props.currentPath || '');
      
      const result = await apiCall('upload', formData, {
        onUploadProgress: (progressEvent) => {
          if (progressEvent.lengthComputable) {
            fileItem.progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          }
        }
      });
      
      if (result && result.success) {
        fileItem.uploading = false;
        fileItem.success = true;
        fileItem.progress = 100;
      } else {
        throw new Error(result?.message || '上传失败');
      }
    } catch (error) {
      fileItem.uploading = false;
      fileItem.error = true;
      fileItem.errorMessage = error.message;
      fileItem.progress = 0;
    }
  });
  
  await Promise.all(uploadPromises);
  
  uploading.value = false;
  
  // 显示上传结果
  const { success, error } = uploadSummary.value;
  if (success > 0) {
    notification.open({
      type: 'success',
      title: '上传完成',
      message: `成功上传 ${success} 个文件${error > 0 ? `，${error} 个失败` : ''}`,
      duration: 3000
    });
    
    // 通知父组件刷新文件列表
    emit('uploaded');
  }
  
  if (error > 0 && success === 0) {
    notification.open({
      type: 'error',
      title: '上传失败',
      message: `所有 ${error} 个文件上传失败`,
      duration: 3000
    });
  }
};

// 格式化文件大小
const formatFileSize = (bytes) => {
  const units = ['B', 'KB', 'MB', 'GB'];
  let size = bytes;
  let unitIndex = 0;
  
  while (size >= 1024 && unitIndex < units.length - 1) {
    size /= 1024;
    unitIndex++;
  }
  
  return `${size.toFixed(1)} ${units[unitIndex]}`;
};
</script>

<style scoped>
