}

.upload-summary {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.summary-stats {
  display: flex;
  gap: 15px;
