<template>
  <div class="search-and-batch-actions">
    <div class="left-section">
      <h3>文件列表</h3>
      <div class="action-buttons">
        <button class="batch-btn upload-btn" @click="$emit('upload-files')">
          <i class="fas fa-upload"></i> 上传文件
        </button>
        <button class="batch-btn" @click="$emit('create-folder')">
          <i class="fas fa-folder-plus"></i> 新建文件夹
        </button>
        <button class="batch-btn" @click="$emit('batch-delete')">
          <i class="fas fa-trash"></i> 批量删除
        </button>
        <button class="batch-btn share-btn" @click="$emit('batch-share')">
          <i class="fas fa-share-alt"></i> 分享
        </button>
        <button class="batch-btn" @click="$emit('refresh-cache')">
          <i class="fas fa-sync-alt"></i> 刷新
        </button>
      </div>
    </div>
    <div class="right-section">
      <div class="search-controls">
        <input
          type="text"
          v-model="searchQuery"
          placeholder="搜索当前文件夹..."
          class="search-input"
          @keyup.enter="handleSearch"
        />
        <button class="batch-btn" @click="handleSearch">
          <i class="fas fa-search"></i> 搜索
        </button>
        <button v-if="isSearchMode" class="batch-btn" @click="clearSearch">
          <i class="fas fa-times"></i> 清除
        </button>
      </div>
    </div>
  </div>
</template>

<script setup>
/* eslint-disable no-undef */
import { ref, watch } from 'vue';

const props = defineProps({
  modelValue: {
    type: String,
    default: ''
  },
  isSearchMode: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['update:modelValue', 'search', 'clear-search', 'batch-delete', 'batch-share', 'refresh-cache', 'create-folder', 'upload-files']);

const searchQuery = ref(props.modelValue);

// 监听外部值变化
watch(() => props.modelValue, (newValue) => {
  searchQuery.value = newValue;
});

// 监听内部值变化并向外传递
watch(searchQuery, (newValue) => {
  emit('update:modelValue', newValue);
});

const handleSearch = () => {
  if (!searchQuery.value.trim()) {
    clearSearch();
    return;
  }
  emit('search', searchQuery.value.trim());
};

const clearSearch = () => {
  searchQuery.value = '';
  emit('clear-search');
};
</script>

<style scoped>
.upload-btn {
  background: linear-gradient(135deg, #007bff, #0056b3) !important;
  color: white !important;
  font-weight: 500;
  border: none !important;
  box-shadow: 0 2px 4px rgba(0, 123, 255, 0.3);
}

.upload-btn:hover {
  background: linear-gradient(135deg, #0056b3, #004085) !important;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(0, 123, 255, 0.4);
}

.share-btn {
  background: linear-gradient(135deg, #28a745, #1e7e34) !important;
  color: white !important;
  font-weight: 500;
  border: none !important;
  box-shadow: 0 2px 4px rgba(40, 167, 69, 0.3);
}

.share-btn:hover {
  background: linear-gradient(135deg, #218838, #155724) !important;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(40, 167, 69, 0.4);
}
</style>
