<template>
  <div v-if="visible" class="dialog-overlay" @click="$emit('close')">
    <div class="dialog" @click.stop>
      <div class="dialog-header">
        <h3>重命名{{ item?.type === 'folder' ? '文件夹' : '文件' }}</h3>
        <button class="dialog-close" @click="$emit('close')">&times;</button>
      </div>
      <div class="dialog-body">
        <label>新名称:</label>
        <input 
          type="text" 
          v-model="newName" 
          class="dialog-input"
          @keyup.enter="handleConfirm"
          ref="nameInput"
        />
      </div>
      <div class="dialog-footer">
        <button class="dialog-btn" @click="$emit('close')">取消</button>
        <button class="dialog-btn primary" @click="handleConfirm">确认</button>
      </div>
    </div>
  </div>
</template>

<script setup>
/* eslint-disable no-undef */
import { ref, watch, nextTick } from 'vue';

const props = defineProps({
  visible: {
    type: Boolean,
    default: false
  },
  item: {
    type: Object,
    default: null
  }
});

const emit = defineEmits(['close', 'confirm']);

const newName = ref('');
const nameInput = ref(null);

// 当弹窗显示时，设置默认值并聚焦输入框
watch(() => props.visible, (visible) => {
  if (visible && props.item) {
    newName.value = props.item.name;
    nextTick(() => {
      if (nameInput.value) {
        nameInput.value.focus();
        nameInput.value.select();
      }
    });
  }
});

const handleConfirm = () => {
  if (!newName.value.trim()) {
    return;
  }
  
  emit('confirm', {
    item: props.item,
    newName: newName.value.trim()
  });
};
</script>
