<template>
  <div v-if="visible" class="dialog-overlay" @click="$emit('close')">
    <div class="dialog" @click.stop>
      <div class="dialog-header">
        <h3>移动{{ item?.type === 'folder' ? '文件夹' : '文件' }}</h3>
        <button class="dialog-close" @click="$emit('close')">&times;</button>
      </div>
      <div class="dialog-body">
        <label>目标路径:</label>
        <input 
          type="text" 
          v-model="targetPath" 
          class="dialog-input"
          placeholder="例如: 2024/7 (空表示根目录)"
          @keyup.enter="handleConfirm"
        />
        <p class="move-hint" v-if="item">当前文件路径: {{ getItemPath(item) }}</p>
      </div>
      <div class="dialog-footer">
        <button class="dialog-btn" @click="$emit('close')">取消</button>
        <button class="dialog-btn primary" @click="handleConfirm">确认</button>
      </div>
    </div>
  </div>
</template>

<script setup>
/* eslint-disable no-undef */
import { ref, watch } from 'vue';

const props = defineProps({
  visible: {
    type: Boolean,
    default: false
  },
  item: {
    type: Object,
    default: null
  },
  currentPath: {
    type: String,
    default: ''
  }
});

const emit = defineEmits(['close', 'confirm']);

const targetPath = ref('');

// 当弹窗显示时，设置默认路径
watch(() => props.visible, (visible) => {
  if (visible) {
    targetPath.value = props.currentPath;
  }
});

// 获取文件路径
const getItemPath = (item) => {
  if (props.currentPath) {
    return `${props.currentPath}/${item.name}`;
  }
  return item.name;
};

const handleConfirm = () => {
  emit('confirm', {
    item: props.item,
    targetPath: targetPath.value.trim()
  });
};
</script>
