<template>
  <div>
    <!-- 图片预览 -->
    <vue-easy-lightbox
      :visible="imagePreview.visible"
      :imgs="imagePreview.images"
      :index="imagePreview.index"
      @hide="closeImagePreview"
    />
    
    <!-- 视频预览弹窗 -->
    <div v-if="videoPreview.visible" class="video-preview-overlay" @click="closeVideoPreview">
      <div class="video-preview-container" @click.stop>
        <div class="video-preview-header">
          <span class="video-title">{{ videoPreview.title }}</span>
          <button class="close-btn" @click="closeVideoPreview">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="video-wrapper">
          <video 
            ref="videoPlayer"
            :src="videoPreview.url" 
            controls 
            preload="metadata"
            style="width: 100%; height: auto; max-height: 70vh;"
            @loadstart="onVideoLoadStart"
            @error="onVideoError"
          >
            您的浏览器不支持 HTML5 视频。
          </video>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { reactive } from 'vue';
import VueEasyLightbox from 'vue-easy-lightbox';

// 图片预览状态
const imagePreview = reactive({
  visible: false,
  images: [],
  index: 0
});

// 视频预览状态
const videoPreview = reactive({
  visible: false,
  url: '',
  title: ''
});

// 视频事件处理
const onVideoLoadStart = () => {
  console.log('视频开始加载');
};

const onVideoError = (event) => {
  console.error('视频加载失败:', event);
};

// 判断是否为图片文件
const isImageFile = (extension) => {
  const imageExtensions = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp', 'svg', 'ico', 'tiff', 'tif'];
  return imageExtensions.includes(extension?.toLowerCase());
};

// 判断是否为视频文件
const isVideoFile = (extension) => {
  const videoExtensions = ['mp4', 'avi', 'mov', 'wmv', 'flv', 'webm', 'mkv', 'm4v', 'rmvb', 'rm', '3gp'];
  return videoExtensions.includes(extension?.toLowerCase());
};

// 生成文件预览URL - 使用JWT令牌
const generatePreviewUrl = (filePath, jwt) => {
  const params = new URLSearchParams();
  params.append('file_path', filePath);
  params.append('action', 'preview');
  if (jwt) {
    params.append('jwt', jwt);
  }
  
  return `/api/file/secure-access.php?${params.toString()}`;
};

// 打开图片预览
const openImagePreview = (file, filePath, allFiles = [], jwt = null) => {
  // 获取当前目录下所有图片文件
  const imageFiles = allFiles.filter(f => 
    f.type === 'file' && isImageFile(f.extension)
  );
  
  // 构建图片URL数组 - 直接使用JWT
  const images = imageFiles.map((imgFile) => {
    const imgPath = filePath.replace(file.name, imgFile.name);
    const previewUrl = generatePreviewUrl(imgPath, jwt);
    return {
      src: previewUrl,
      title: imgFile.name
    };
  });
  
  // 找到当前文件的索引
  const currentIndex = imageFiles.findIndex(imgFile => imgFile.name === file.name);
  
  imagePreview.images = images;
  imagePreview.index = Math.max(0, currentIndex);
  imagePreview.visible = true;
};

// 关闭图片预览
const closeImagePreview = () => {
  imagePreview.visible = false;
  imagePreview.images = [];
  imagePreview.index = 0;
};

// 打开视频预览
const openVideoPreview = (file, filePath, jwt = null) => {
  const previewUrl = generatePreviewUrl(filePath, jwt);
  videoPreview.url = previewUrl;
  videoPreview.title = file.name;
  videoPreview.visible = true;
};

// 关闭视频预览
const closeVideoPreview = () => {
  videoPreview.visible = false;
  videoPreview.url = '';
  videoPreview.title = '';
};

// 预览文件 - 主要入口函数
const previewFile = (file, filePath, allFiles = [], jwt = null) => {
  if (!file || file.type !== 'file') {
    console.warn('只能预览文件类型');
    return false;
  }
  
  const extension = file.extension?.toLowerCase();
  
  try {
    if (isImageFile(extension)) {
      openImagePreview(file, filePath, allFiles, jwt);
      return true;
    } else if (isVideoFile(extension)) {
      openVideoPreview(file, filePath, jwt);
      return true;
    }
  } catch (error) {
    console.error('预览失败:', error);
    return false;
  }
  
  return false; // 不支持预览的文件类型
};

// 暴露方法给父组件
// eslint-disable-next-line no-undef
defineExpose({
  previewFile,
  isImageFile,
  isVideoFile
});
</script>

<style scoped>
/* 视频预览弹窗样式 */
.video-preview-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.video-preview-container {
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  max-width: 90vw;
  max-height: 90vh;
  width: 800px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.video-preview-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  background: #f5f5f5;
  border-bottom: 1px solid #e0e0e0;
}

.video-title {
  font-weight: 500;
  font-size: 16px;
  color: #333;
}

.close-btn {
  background: none;
  border: none;
  font-size: 16px;
  color: #666;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s;
}

.close-btn:hover {
  background: #e0e0e0;
  color: #333;
}

.video-wrapper {
  padding: 0;
  background: #000;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .video-preview-container {
    width: 95vw;
    max-height: 80vh;
  }
  
  .video-preview-header {
    padding: 12px 16px;
  }
  
  .video-title {
    font-size: 14px;
  }
}
</style>
