<template>
  <div class="file-list-container">
    <!-- 卡片容器 -->
    <div class="file-card">
      <!-- 搜索框和批量操作栏 -->
      <FileListActions
        v-model="searchQuery"
        :is-search-mode="isSearchMode"
        @search="handleSearch"
        @clear-search="clearSearch"
        @batch-delete="handleBatchDelete"
        @batch-share="handleBatchShare"
        @refresh-cache="refreshCache"
        @create-folder="handleCreateFolder"
        @upload-files="showUploadDialog"
      />

      <!-- 路径导航和缓存信息 -->
      <div class="path-info">
        <div class="current-path">
          <span @click="navigateToPath('')" class="path-link">根目录</span>
          <template v-if="currentPath">
            <span v-for="(segment, index) in pathSegments" :key="index">
              <span class="path-separator"> / </span>
              <span 
                @click="navigateToPath(pathSegments.slice(0, index + 1).join('/'))" 
                class="path-link"
              >
                {{ segment }}
              </span>
            </span>
          </template>
        </div>
        <div class="cache-info" v-if="cacheInfo">
          <span>缓存时间: {{ formatTime(cacheInfo.created_time) }}</span>
          <span v-if="cacheInfo.from_cache"> (来自缓存)</span>
        </div>
      </div>

      <!-- 文件列表 -->
      <FileListTable
        :files="currentPageFiles"
        :selected-files="selectedFiles"
        :is-loading="isLoading"
        :is-search-mode="isSearchMode"
        @toggle-all="toggleAll"
        @update:selected-files="selectedFiles = $event"
        @rename="handleRename"
        @move="handleMove"
        @download="handleDownload"
        @delete="handleDelete"
        @enter-folder="handleEnterFolder"
        @preview="handlePreview"
      />

      <!-- 分页组件 -->
      <FileListPagination
        :pagination="pagination"
        @change-page="changePage"
      />
    </div>

    <!-- 重命名弹窗 -->
    <FileRenameDialog
      :visible="renameDialog.visible"
      :item="renameDialog.item"
      @close="closeRenameDialog"
      @confirm="confirmRename"
    />

    <!-- 移动弹窗 -->
    <FileMoveDialog
      :visible="moveDialog.visible"
      :item="moveDialog.item"
      :current-path="currentPath"
      @close="closeMoveDialog"
      @confirm="confirmMove"
    />

    <!-- 创建文件夹弹窗 -->
        <!-- 文件夹创建弹窗 -->
    <FileCreateFolderDialog
      :visible="createFolderDialog.visible"
      :parent-path="currentPath"
      @close="closeCreateFolderDialog"
      @confirm="confirmCreateFolder"
    />

    <!-- 文件上传弹窗 -->
    <FileUploadDialog
      :visible="uploadDialog.visible"
      :current-path="currentPath"
      @close="closeUploadDialog"
      @uploaded="handleFileUploaded"
    />

    <!-- 文件预览组件 -->
    <FilePreview
      ref="filePreviewRef"
    />
  </div>
</template>

<script setup>
import { ref, computed, onMounted, inject } from 'vue';
import { ensureVisitJwt, getVisitJwt } from '@/utils/auth.js';
import FileListActions from './FileListActions.vue';
import FileListTable from './FileListTable.vue';
import FileListPagination from './FileListPagination.vue';
import FileRenameDialog from './FileRenameDialog.vue';
import FileMoveDialog from './FileMoveDialog.vue';
import FileCreateFolderDialog from '@/panel/child/file/FileCreateFolderDialog.vue';
import FileUploadDialog from '@/panel/child/file/FileUploadDialog.vue';
import FilePreview from '@/panel/child/file/FilePreview.vue';

// 注入全局组件
const notification = inject('notification');
const confirm = inject('confirm');

// 组件引用
const filePreviewRef = ref(null);

// 状态管理
const isLoading = ref(false);
const allFiles = ref([]);
const searchResults = ref([]);
const isSearchMode = ref(false);
const searchQuery = ref('');
const selectedFiles = ref([]);
const currentPage = ref(1);
const pageSize = ref(20);
const currentPath = ref('');
const pagination = ref(null);
const cacheInfo = ref(null);

// 弹窗状态
const renameDialog = ref({
  visible: false,
  item: null
});

const moveDialog = ref({
  visible: false,
  item: null
});

const createFolderDialog = ref({
  visible: false
});

const uploadDialog = ref({
  visible: false
});

// 计算属性
const currentPageFiles = computed(() => {
  return isSearchMode.value ? searchResults.value : allFiles.value;
});

const pathSegments = computed(() => {
  return currentPath.value ? currentPath.value.split('/').filter(Boolean) : [];
});

// 生成唯一文件ID
const getFileId = (file) => {
  return `${file.type}_${file.name}_${file.modified}`;
};

// 获取文件路径
const getItemPath = (item) => {
  if (currentPath.value) {
    return `${currentPath.value}/${item.name}`;
  }
  return item.name;
};

// API 调用函数
const apiCall = async (action, data = {}, options = {}) => {
  try {
    // 确保有访问JWT
    const hasJwt = await ensureVisitJwt();
    if (!hasJwt) {
      notification.open({
        type: 'error',
        title: '认证失败',
        message: '请重新登录',
        onClose: () => window.location.reload()
      });
      return null;
    }

    const formData = new FormData();
    formData.append('action', action);
    
    // 添加JWT访问令牌
    const visitJwt = getVisitJwt();
    if (visitJwt) {
      formData.append('jwt', visitJwt);
    }
    
    // 添加其他参数
    for (const [key, value] of Object.entries(data)) {
      if (value !== null && value !== undefined && value !== '') {
        formData.append(key, value);
      }
    }

    const response = await fetch('/api/file/file-api.php', {
      method: 'POST',
      body: formData,
      credentials: 'include'
    });

    if (options.isDownload) {
      return response;
    }

    const result = await response.json();
    
    if (!result.success) {
      notification.open({
        type: 'error',
        title: '操作失败',
        message: result.message || '未知错误'
      });
      return null;
    }
    
    return result;
  } catch (error) {
    console.error('API调用失败:', error);
    notification.open({
      type: 'error',
      title: '网络错误',
      message: '请检查网络连接后重试'
    });
    return null;
  }
};

// 加载文件列表
const loadFileList = async (page = 1, forceRefresh = false) => {
  isLoading.value = true;
  
  try {
    const result = await apiCall('list', {
      path: currentPath.value,
      page: page,
      per_page: pageSize.value,
      force_refresh: forceRefresh
    });
    
    if (result) {
      allFiles.value = result.data.items || [];
      pagination.value = result.pagination;
      cacheInfo.value = {
        created_time: result.data.created_time,
        from_cache: result.from_cache
      };
      currentPage.value = page;
      selectedFiles.value = [];
    }
  } finally {
    isLoading.value = false;
  }
};

// 搜索文件
const handleSearch = async (keyword) => {
  isLoading.value = true;
  
  try {
    const result = await apiCall('search', {
      path: currentPath.value,
      keyword: keyword,
      page: 1,
      per_page: pageSize.value
    });
    
    if (result) {
      searchResults.value = result.data || [];
      pagination.value = result.pagination;
      isSearchMode.value = true;
      currentPage.value = 1;
      selectedFiles.value = [];
    }
  } finally {
    isLoading.value = false;
  }
};

// 清除搜索
const clearSearch = () => {
  searchQuery.value = '';
  isSearchMode.value = false;
  searchResults.value = [];
  loadFileList(1);
};

// 刷新缓存
const refreshCache = async () => {
  await loadFileList(currentPage.value, true);
  notification.open({
    type: 'success',
    title: '缓存已刷新',
    message: '文件列表已更新',
    duration: 3000
  });
};

// 创建文件夹
const handleCreateFolder = () => {
  createFolderDialog.value.visible = true;
};

const closeCreateFolderDialog = () => {
  createFolderDialog.value.visible = false;
};

const confirmCreateFolder = async (data) => {
  const { folderName } = data;
  
  const result = await apiCall('create_folder', {
    parent_path: currentPath.value,
    folder_name: folderName
  });
  
  if (result) {
    notification.open({
      type: 'success',
      title: '创建成功',
      message: `文件夹 "${folderName}" 已创建`,
      duration: 3000
    });
    
    closeCreateFolderDialog();
    
    if (isSearchMode.value) {
      handleSearch(searchQuery.value);
    } else {
      // 强制刷新缓存，确保显示最新状态
      loadFileList(currentPage.value, true);
    }
  }
};

// 文件上传
const showUploadDialog = () => {
  uploadDialog.value.visible = true;
};

const closeUploadDialog = () => {
  uploadDialog.value.visible = false;
};

const handleFileUploaded = () => {
  // 文件上传完成后刷新文件列表
  if (isSearchMode.value) {
    handleSearch(searchQuery.value);
  } else {
    // 强制刷新缓存，确保显示最新上传的文件
    loadFileList(currentPage.value, true);
  }
  
  notification.open({
    type: 'success',
    title: '上传完成',
    message: '文件列表已更新',
    duration: 2000
  });
};

// 换页
const changePage = (page) => {
  if (isSearchMode.value) {
    handleSearch(searchQuery.value);
  } else {
    loadFileList(page);
  }
};

// 进入文件夹
const handleEnterFolder = (folder) => {
  if (folder.type !== 'folder') return;
  
  // 更新当前路径
  if (currentPath.value) {
    currentPath.value = `${currentPath.value}/${folder.name}`;
  } else {
    currentPath.value = folder.name;
  }
  
  // 清除搜索状态
  clearSearch();
  
  // 重新加载文件列表
  loadFileList(1);
};

// 路径导航
const navigateToPath = (path) => {
  currentPath.value = path;
  clearSearch();
  loadFileList(1);
};

// 全选/反选
const toggleAll = (e) => {
  if (e.target.checked) {
    selectedFiles.value = currentPageFiles.value.map(file => getFileId(file));
  } else {
    selectedFiles.value = [];
  }
};

// 删除文件
const handleDelete = async (file) => {
  confirm.open({
    title: '确认删除',
    message: `确定要删除${file.type === 'folder' ? '文件夹' : '文件'}"${file.name}"吗？此操作不可恢复！`,
    onConfirm: async () => {
      const action = file.type === 'folder' ? 'delete_folder' : 'delete_file';
      const paramName = file.type === 'folder' ? 'folder_path' : 'file_path';
      
      const result = await apiCall(action, {
        [paramName]: getItemPath(file),
        force_delete: true
      });
      
      if (result) {
        notification.open({
          type: 'success',
          title: '删除成功',
          message: `${file.type === 'folder' ? '文件夹' : '文件'}已删除`,
          duration: 3000
        });
        
        if (isSearchMode.value) {
          handleSearch(searchQuery.value);
        } else {
          // 强制刷新缓存，确保显示最新状态
          loadFileList(currentPage.value, true);
        }
      }
    }
  });
};

// 批量删除
const handleBatchDelete = async () => {
  if (selectedFiles.value.length === 0) {
    notification.open({
      type: 'warning',
      title: '提示',
      message: '请选择要删除的文件',
      duration: 3000
    });
    return;
  }
  
  confirm.open({
    title: '确认批量删除',
    message: `确定要删除选中的 ${selectedFiles.value.length} 个项目吗？此操作不可恢复！`,
    onConfirm: async () => {
      const promises = [];
      
      for (const fileId of selectedFiles.value) {
        const file = currentPageFiles.value.find(f => getFileId(f) === fileId);
        if (file) {
          const action = file.type === 'folder' ? 'delete_folder' : 'delete_file';
          const paramName = file.type === 'folder' ? 'folder_path' : 'file_path';
          
          promises.push(apiCall(action, {
            [paramName]: getItemPath(file),
            force_delete: true
          }));
        }
      }
      
      await Promise.all(promises);
      
      notification.open({
        type: 'success',
        title: '批量删除完成',
        message: `已删除 ${selectedFiles.value.length} 个项目`,
        duration: 3000
      });
      
      selectedFiles.value = [];
      
      if (isSearchMode.value) {
        handleSearch(searchQuery.value);
      } else {
        // 强制刷新缓存，确保显示最新状态
        loadFileList(currentPage.value, true);
      }
    }
  });
};

// 批量分享
const handleBatchShare = async () => {
  if (selectedFiles.value.length === 0) {
    notification.open({
      type: 'warning',
      title: '提示',
      message: '请选择要分享的文件',
      duration: 3000
    });
    return;
  }
  
  // 过滤出文件（排除文件夹）
  const filesToShare = [];
  for (const fileId of selectedFiles.value) {
    const file = currentPageFiles.value.find(f => getFileId(f) === fileId);
    if (file && file.type !== 'folder') {
      const filePath = getItemPath(file);
      filesToShare.push(filePath);
    }
  }
  
  if (filesToShare.length === 0) {
    notification.open({
      type: 'warning',
      title: '提示',
      message: '只能分享文件，不能分享文件夹',
      duration: 3000
    });
    return;
  }
  
  
  try {
    // 确保有访问JWT
    const hasJwt = await ensureVisitJwt();
    if (!hasJwt) {
      notification.open({
        type: 'error',
        title: '认证失败',
        message: '请重新登录',
        onClose: () => window.location.reload()
      });
      return;
    }

    const formData = new FormData();
    formData.append('action', 'add_files');
    
    // 添加JWT访问令牌
    const visitJwt = getVisitJwt();
    if (visitJwt) {
      formData.append('jwt', visitJwt);
    }
    
    // 添加文件路径数组 - 使用JSON字符串格式
    const filePathsJson = JSON.stringify(filesToShare);
    formData.append('file_paths', filePathsJson);

    const result = await fetch('/api/share/share-api.php', {
      method: 'POST',
      body: formData,
      credentials: 'include'
    });
    
    const response = await result.json();
    
    if (response.success) {
      const { data } = response;
      const { added_count, skipped_count, error_count} = data;
      
      // 构建详细的结果消息
      let message = '';
      let messageType = 'success';
      
      if (added_count > 0 && skipped_count === 0 && error_count === 0) {
        // 全部成功
        message = `已将 ${added_count} 个文件添加到分享列表`;
        messageType = 'success';
      } else if (added_count > 0) {
        // 部分成功
        message = `已添加 ${added_count} 个文件`;
        if (skipped_count > 0) {
          message += `，跳过 ${skipped_count} 个文件（已在分享列表中）`;
        }
        if (error_count > 0) {
          message += `，${error_count} 个文件处理失败`;
        }
        messageType = 'warning';
      } else if (skipped_count > 0 && error_count === 0) {
        // 全部跳过
        message = `选中的 ${skipped_count} 个文件已在分享列表中`;
        messageType = 'info';
      } else {
        // 全部失败或混合情况
        message = '分享操作完成：';
        if (skipped_count > 0) {
          message += `${skipped_count} 个文件已在列表中，`;
        }
        if (error_count > 0) {
          message += `${error_count} 个文件处理失败`;
        }
        messageType = 'warning';
      }
      
      notification.open({
        type: messageType,
        title: '分享结果',
        message: message,
        duration: messageType === 'info' ? 4000 : 3000
      });
      
      selectedFiles.value = [];
    } else {
      throw new Error(response.message);
    }
  } catch (error) {
    console.error('分享失败:', error); // 调试信息
    notification.open({
      type: 'error',
      title: '分享失败',
      message: error.message || '添加到分享列表失败'
    });
  }
};

// 重命名相关
const handleRename = (file) => {
  renameDialog.value = {
    visible: true,
    item: file
  };
};

const closeRenameDialog = () => {
  renameDialog.value.visible = false;
};

const confirmRename = async (data) => {
  const { item, newName } = data;
  
  if (!newName.trim()) {
    notification.open({
      type: 'warning',
      title: '错误',
      message: '名称不能为空',
      duration: 3000
    });
    return;
  }
  
  if (newName === item.name) {
    closeRenameDialog();
    return;
  }
  
  const action = item.type === 'folder' ? 'rename_folder' : 'rename_file';
  const paramName = item.type === 'folder' ? 'folder_path' : 'file_path';
  
  const result = await apiCall(action, {
    [paramName]: getItemPath(item),
    new_name: newName.trim()
  });
  
  if (result) {
    notification.open({
      type: 'success',
      title: '重命名成功',
      message: `${item.type === 'folder' ? '文件夹' : '文件'}已重命名`,
      duration: 3000
    });
    
    closeRenameDialog();
    
    if (isSearchMode.value) {
      handleSearch(searchQuery.value);
    } else {
      // 强制刷新缓存，确保显示最新状态
      loadFileList(currentPage.value, true);
    }
  }
};

// 移动相关
const handleMove = (file) => {
  moveDialog.value = {
    visible: true,
    item: file
  };
};

const closeMoveDialog = () => {
  moveDialog.value.visible = false;
};

const confirmMove = async (data) => {
  const { item, targetPath } = data;
  
  const action = item.type === 'folder' ? 'move_folder' : 'move_file';
  const paramName = item.type === 'folder' ? 'folder_path' : 'file_path';
  
  // 记录原始路径和目标路径，用于缓存刷新
  const originalPath = currentPath.value;
  const itemOriginalPath = getItemPath(item);
  const itemOriginalDir = itemOriginalPath.includes('/') ? itemOriginalPath.substring(0, itemOriginalPath.lastIndexOf('/')) : '';
  
  const result = await apiCall(action, {
    [paramName]: itemOriginalPath,
    target_path: targetPath.trim()
  });
  
  if (result) {
    notification.open({
      type: 'success',
      title: '移动成功',
      message: `${item.type === 'folder' ? '文件夹' : '文件'}已移动`,
      duration: 3000
    });
    
    closeMoveDialog();
    
    // 智能刷新策略：
    // 1. 如果当前在源文件夹，刷新当前文件列表
    // 2. 如果移动到当前文件夹，也需要刷新
    // 3. 始终强制刷新以确保缓存更新
    const needRefresh = originalPath === itemOriginalDir || 
                       originalPath === targetPath.trim() || 
                       currentPath.value === itemOriginalDir ||
                       currentPath.value === targetPath.trim();
    
    if (needRefresh) {
      if (isSearchMode.value) {
        handleSearch(searchQuery.value);
      } else {
        // 强制刷新缓存，确保显示最新状态
        loadFileList(currentPage.value, true);
      }
    } else {
      // 即使不在相关目录，也刷新当前目录（移除已移动的项目）
      if (isSearchMode.value) {
        handleSearch(searchQuery.value);
      } else {
        loadFileList(currentPage.value, true);
      }
    }
  }
};

// 下载文件 - 使用JWT令牌直接下载
const handleDownload = async (file) => {
  try {
    // 确保有访问JWT
    const hasJwt = await ensureVisitJwt();
    if (!hasJwt) {
      notification.open({
        type: 'error',
        title: '认证失败',
        message: '请重新登录',
        onClose: () => window.location.reload()
      });
      return;
    }

    const visitJwt = getVisitJwt();
    const filePath = getItemPath(file);
    const fileSize = file.size || 0;
    const sizeMB = fileSize / (1024 * 1024);
    
    // 构建下载URL
    const params = new URLSearchParams();
    params.append('file_path', filePath);
    params.append('action', 'download');
    params.append('jwt', visitJwt);
    
    const downloadUrl = `/api/file/secure-access.php?${params.toString()}`;
    
    // 创建下载链接
    const a = document.createElement('a');
    a.href = downloadUrl;
    a.download = file.name;
    a.target = '_blank';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    
    notification.open({
      type: 'success',
      title: '下载开始',
      message: `文件 "${file.name}" ${sizeMB > 50 ? '(大文件)' : ''} 下载已开始`,
      duration: sizeMB > 50 ? 5000 : 3000
    });
    
  } catch (error) {
    console.error('下载失败:', error);
    notification.open({
      type: 'error',
      title: '下载失败',
      message: error.message || '下载过程中发生错误',
      duration: 5000
    });
  }
};

// 文件预览
const handlePreview = (file) => {
  if (!filePreviewRef.value) {
    console.warn('预览组件未准备就绪');
    return;
  }
  
  const filePath = getItemPath(file);
  const jwt = getVisitJwt(); // 获取当前JWT
  const success = filePreviewRef.value.previewFile(file, filePath, currentPageFiles.value, jwt);
  
  if (!success) {
    notification.open({
      type: 'info',
      title: '预览提示',
      message: `文件类型 "${file.extension || '未知'}" 暂不支持预览`,
      duration: 3000
    });
  }
};

// 工具函数
const formatTime = (timestamp) => {
  return new Date(timestamp * 1000).toLocaleString('zh-CN');
};

// 组件挂载
onMounted(() => {
  loadFileList(1);
});
</script>

<style>
@import './file-list.css';
</style>
